.sidebar {
    display: none;
}

.sidebar.open {
    width: 200px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    position: fixed;
    height: 100%;
    background-color: #FFFFFF;
    z-index: 1;
}

.background {
    position: absolute;
    background-color: rgba(6,4,4,0.31);
    width: 100%;
    height: 100%;
}

.nav {
    display: flex;
    flex-direction: column;
    margin: 30px;
    font-size: 20px;
    line-height: 40px;
}

.nav a {
    color: black;
    text-decoration: none;
}

.nav a:hover {
    color: #0a9ee5;
    text-decoration: none;
}
.nav a:active {
    color: #5992ad;
    text-decoration: none;
}

.nav a.active {
    color: #0066cc;
    text-decoration: #0066cc underline;
}

.close {
    width: 30px;
    height: 30px;
    background-color: transparent;
    border: none;
}

.close img {
}
